.order_con{
    max-width: 110rem;
    margin: 8rem auto 3rem auto;
}
.ren_ord{
    display: flex;
    flex-wrap: wrap;
}
.orderItems{
    width: 25rem;
    margin: 1rem;
    box-shadow: 0px 0px 6px #dadada;
}
.ord_img{
    width:100%;
    height: 20rem;
}
.ord_img > img{
    height: 100%;
    width:100%;
    object-fit: cover;
}
.order_ttl{
    font-size: 3rem;
}
.ord_cnt{
    padding: 1rem;
}
.ord_cnt h4{
    font-size: 1.7rem;
    margin-bottom: 0;
}
.ord_prc{
    font-size: 2.4rem;
}
.ord_els{
    max-width: 110rem;
    margin: 8rem auto 5rem auto;
}
.ord_els h2{
    font-size: 3rem;
    text-align: center;
}

.ord_frm, .ren_ord, .ord_prc_con, .chk_btn{
    position: relative;
    float: left;
    width: 100%;
}
.dis_ord{
    max-width: 110rem;
    margin: 8rem auto 5rem auto;
    overflow: auto;
}

/* Media Query */
@media only screen and (max-width: 1280px){
    .order_ttl {
        font-size: 2.5rem;
    }
    .orderItems{
        width: 24rem;  
    }
    .ord_img{
        height: 16rem;   
    }
    .ord_cnt h4 {
        font-size: 1.4rem;
    }
    .ord_prc {
        font-size: 2rem;
    }
    .dis_ord_ttl{
        font-size: 2.4rem;
    }
}
@media only screen and (max-width: 768px){
    .order_ttl {
        font-size: 2rem;
    }
}
@media only screen and (max-width: 600px){
    .ord_cnt h4 {
        font-size: 1.3rem;
    }
    .orderItems {
        width: 100%;
    }
    .order_ttl {
        font-size: 1.7rem;
    }
    .dis_ord_ttl{
        font-size: 2rem;
    }
}